<template>
  <div>
    <div class="header">
      <img src="../assets/logo.png" alt="" class="logo">
      政企智慧服务平台
    </div>
    <div class="body">
      <div class="login">
        <span class="login-1" style="cursor:pointer;" @click="toLogin">登陆</span>
        &nbsp;&nbsp;
        <span style=" color: rgba(3, 87, 255, 0.749019607843137);cursor: pointer">注册</span>
      </div>
      <div class="loginForm">
        <div class="loginL">
          <ul>
            <li> <span style="color: #E84343">*</span> 用户名&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 密码&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 确认密码&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 邮箱号&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 验证码&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 邮箱验证码&nbsp;&nbsp;:&nbsp;</li>
          </ul>
        </div>
        <div class="loginR">
          <ul >
            <li>
<!--              <input type="text" placeholder="4-20位字符" style="padding-left: 5px" v-model="userName" @change="judUser">-->
              <el-input v-model="userName" placeholder="4-20位字符" @change="judUser" style="padding-left: 5px;width: 128%"></el-input>
            </li>
            <li>
<!--              <input type="password" placeholder="8-20位字符,至少包含大写字母、小写字母、数字、特殊符号中的两种" style="padding-left: 5px" ref="password"  v-model="password" >-->
              <el-input placeholder="8-20位字符,至少包含大写字母、小写字母、数字、特殊符号中的两种" style="padding-left: 5px;width: 128%" v-model="password" show-password></el-input>
<!--              <img src="../assets/eye.png" alt="" style="z-index: 999" @click="togglePassword">-->
            </li>
            <li>
<!--              <input type="password" v-model="repassword" @change="isEqual">-->
              <el-input placeholder="" v-model="repassword" show-password @change="isEqual" style="padding-left: 5px;width: 128%"></el-input>
            </li>
            <li>
<!--              <input type="text" v-model="mail" @change="regMain">-->
              <el-input v-model="mail" @change="regMain" style="padding-left: 5px;width: 128%" ></el-input>
            </li>
            <li class="verify">
              <el-input v-model="verify" style="padding-left: 5px;width: 92%"></el-input>
              <img src="http://192.168.30.43:8080/getCode" alt="" @click="toggleVerify" ref="verify">
              <span @click="toggleVerify" style="cursor:pointer;">看不清楚？点击刷新</span>
            </li>
            <li class="phoneVerify">
              <el-input v-model="mailVerify" style="padding-left: 5px;width: 92%"></el-input>
              <button @click="getMailVerify">获取验证码</button>
            </li>
            <li class="treaty">
              <el-checkbox v-model="checked">我已阅读并同意《安徽政务服务网个人用户注册协议》</el-checkbox>
            </li>
            <li class="register">
              <input type="button" value="注册" @click="toRegister">
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="footer">
      <div>
        <span>关于本站</span>
        <span>版权声明</span>
        <span>常见问题</span>
      </div>
      <div>
        <span>版权所有：国家高新技术产业开发区管委会 主办方：国家高新区大数据局</span>
      </div>
      <div>
        <span>网站备案号：皖ICP备10207486号 　网站标识码：3708000041</span>
      </div>
      <div>
        <span>党政机关网站标识号：CA162410000604238070001        皖公网安备 37089702000262号</span>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "register",
        data(){
            return{
                userName:'',
                password:'',
                repassword:'',
                mail:'',
                verify:'',
                mailVerify:'',
                checked:false,
                passwordSign:true
            }
        },
        methods:{
            //邮箱匹配
            regMain(){
                var strReg="";
                var r;
                var strText=this.mail;
                strReg=/^\w+((-\w+)|(\.\w+))*\@{1}\w+\.{1}\w{2,4}(\.{0,1}\w{2}){0,1}/ig;
                r=strText.search(strReg);
                console.log(r)
                if (r == -1){
                    this.$message({
                        message: '请输入合法的邮箱',
                        type: 'warning'
                    });
                    return false
                }
            },
            //密码显示隐藏
            togglePassword(){
                var password = this.$refs.password;
                if (this.passwordSign){
                    password.type="text";
                    this.passwordSign=false;
                }else  {
                    password.type="password";
                    this.passwordSign=true;
                }

                console.log(password)
            },
            toLogin() {
                this.$router.push('/login')
            },
            toggleVerify(){
                var that = this;
                var img = this.$refs.verify;
                img.src="http://192.168.30.43:8080/getCode?"+Math.random();
            },
            //用户名合法
            judUser(){
                var userName = this.userName;
                if (userName.length<4){
                    this.$message({
                        message: '请输入4位以上用户名',
                        type: 'warning'
                    });
                    return false
                }
                if (userName.length>20){
                    this.$message({
                        message: '请输入20位以下用户名',
                        type: 'warning'
                    });
                    return false
                }

            },
            //获取邮箱验证码
            getMailVerify(){
                // 检验验证码
                var that = this;
                var email = this.mail;
                let data = new FormData();
                data.append('code',this.verify);
                this.$axios.get('http://192.168.30.43:8080/verificationCode?code='+that.verify).then(function(res) {
                    //邮箱验证码
                    if (res.data.code==0){
                        that.$axios.post('http://192.168.30.43:8080/user/sendCode', that.$qs.stringify({
                            email:email
                        })).then(function(res) {
                            if (res.data.code==0){
                                that.$message({
                                    message: '邮件已发发送!请检查邮箱',
                                    type: 'success'
                                });
                            }else {
                                that.$message({
                                    message: res.data.msg,
                                    type: 'warning'
                                });
                            }
                            console.log(res);
                        })
                            .catch(err=>{
                                that.$message({
                                    message: '网络错误',
                                    type: 'warning'
                                });
                            })
                    }
                    console.log(res);
                })
            },
            //是否相等
            isEqual(){
                var password = this.password;
                var repassword = this.repassword;
                if (password != repassword){
                    this.$message({
                        message: '两次密码输入不一致',
                        type: 'warning'
                    });
                }
            },
            //注册
            toRegister(){
                var that = this;
                if (this.userName==''){
                    this.$message({
                        message: '请输入用户名',
                        type: 'warning'
                    });
                    return false
                }
                if (this.userName.length < 4){
                    this.$message({
                        message: '请输入4位以上用户名',
                        type: 'warning'
                    });
                    return false
                }
                if (this.userName.length > 20){
                    this.$message({
                        message: '请输入20位以下用户名',
                        type: 'warning'
                    });
                    return false
                }
                if (this.password==''){
                    this.$message({
                        message: '请输入密码',
                        type: 'warning'
                    });
                    return false
                }
                if (this.repassword==''){
                    this.$message({
                        message: '请输入重复密码',
                        type: 'warning'
                    });
                    return false
                }
                if (this.password != this.repassword){
                    this.$message({
                        message: '两次输入密码不相同',
                        type: 'warning'
                    });
                    return false
                }
                if (this.mail==''){
                    this.$message({
                        message: '请输入邮箱号',
                        type: 'warning'
                    });
                    return false
                }
                if (this.verify==''){
                    this.$message({
                        message: '请输入验证码',
                        type: 'warning'
                    });
                    return false
                }
                if (this.mailVerify==''){
                    this.$message({
                        message: '请输入邮箱验证码',
                        type: 'warning'
                    });
                    return false
                }
                if (!this.checked){
                    this.$message({
                        message: '请同意用户协议',
                        type: 'warning'
                    });
                    return false
                }
                that.$axios.post('http://192.168.30.43:8080/user/registerUser', that.$qs.stringify({
                    userName:that.userName,
                    password:that.password,
                    email:that.mail,
                    status:that.mailVerify
                })).then(function(res) {
                    if (res.data.code==0){
                        that.$message({
                            message: '账号已注册成功',
                            type: 'success'
                        });
                    }else {
                        that.$message.error(res.data.msg);
                    }
                    console.log(res);
                })

            }
        }
    }
</script>

<style scoped>
  *{
    margin: 0;
    padding: 0;
  }
  .login span:nth-child(1){
    font-size: 18px;
    font-weight: 650;
    display: inline-block;
    position: relative;
  }
  .login span:nth-child(2) {
    font-size: 18px;
    color: #333333;
    font-weight: 650;
    position: relative;
    display: inline-block;
  }
  .login-1:after {
    content: '';
    display: inline-block;
    width: 1px;
    height: 28px;
    background-color: #797979;
    position: absolute;
    right: -12px;
    top: 0;
  }
  .login {
    text-align: center;
    margin-bottom: 30px;
    margin-top: 90px;
  }
  .header {
    height: 60px;
    background-color: #4382ff;
    font-size: 18px;
    color: #ffffff;
    font-weight: 600;
    line-height: 60px;
    padding-left: 72px;
    position: relative;
  }
  .logo {
    position: absolute;
    left: 31px;
    top: 50%;
    transform: translateY(-50%);
  }
  .footer {
    width: 100%;
    min-width: 1025px;
    height: 133px;
    background-color: rgba(3, 87, 255, 0.749019607843137);
    font-size: 12px;
    color: #FFFFFF;
    margin-top: 10px;
    box-sizing: border-box;
  }
  .footer> div{
    text-align: center;
  }
  .footer>div:nth-child(1) {
    text-align: center;
    padding-top: 35px;
    padding-bottom: 19px;
  }
  .footer>div:nth-child(1) span:nth-child(1){
    padding-right: 115px;
  }
  .footer>div:nth-child(1)span:nth-child(3){
    padding-left: 115px;
  }
  .loginForm {
    margin: 0 auto;
    width: 578px;
    height: 598px;
    position: relative;
  }
  .loginL {
    width: 100px;
    position: absolute;
    left: 0;
    top: 0;
  }
  .loginL ul li{
    list-style: none;
    height: 59px;
    line-height: 59px;
  }
  .loginL ul li{
    font-size: 14px;
    text-align: right;
  }
  .loginR {
    padding-left: 100px;
  }
  .loginR ul li {
    list-style: none;
  }
  .loginR ul li  {
    height: 59px;
    line-height: 59px;
    width: 354px;
    margin-left: 20px;
  }
  .loginR ul li input {
    height: 32px;
    width: 449px;
  }
  .loginR ul li:nth-child(2) {
    position: relative;
  }
  .loginR ul li:nth-child(2) img {
    position: absolute;
    right: -98px;
    top: 21px;
  }
  .verify input{
    width: 326px!important;
  }
  .phoneVerify input{
    width: 326px!important;
  }
  .verify {
    position: relative;
  }
  .verify img {
    position: absolute;
    right: -98px;
    top: 15px;
    cursor: pointer;
  }
  .verify span {
    position: absolute;
    font-size: 12px;
    color: #347AD4;
    text-decoration: underline;
    width: 114px;
    cursor: pointer;
    right: -225px;
    top: 10px;
  }
  .phoneVerify {
    position: relative;
  }
  .phoneVerify button {
    position: absolute;
    width: 104px;
    height: 32px;
    background-color: rgba(3, 87, 255, 0.749019607843137);
    border-radius: 5px;
    border: none;
    outline: none;
    color: #FFFFFF;
    right: -99px;
    top: 16px;
    cursor: pointer;
  }
  .treaty input{
    width: 12px!important;
    height: 12px!important;
  }
  .treaty span {
    font-size: 13px;
    color: #333333;
  }
  .treaty {
    height: 43px!important;
    line-height: 43px!important;
  }
  .register input{
    height: 47px!important;
    background-color: rgba(3, 87, 255, 0.749019607843137);
    border: none;
    font-size: 20px;
    color: #FFFFFF;
    border-radius: 5px;
  }
  .register {
    margin-top: 31px;
  }
</style>



